import { offers } from '../../offers';

const state = {
	count: 0,
	active: offers
};

const getters = {
	count() {
		return state.count;
	},
	active() {
		return state.active;
	}
};

const actions = {
	get(context, payload) {
		window.scrollTo({ top: 0 });

		let queryStr = ``;

		if (payload) {
			queryStr = `(`;
			for (let key in payload) {
				if (typeof payload[key] === 'string') {
					payload[key] = `"${payload[key]}"`;
				}
				queryStr += `${key}: ${payload[key]}, `;
			}
			queryStr = queryStr.slice(0, -2).concat(')');
		}
		// TODO убрать ненужные свойства
		let query = `
        {
  paginateOffer ${queryStr ? queryStr : ''} {
    count
    total
    offers{
      id
      name
      prices{
        sum
        discount
      }
      images{
        id
        seoname
      }
    }
  }
}`;

		// console.log(query);
		context.commit('loading/SET', true, { root: true });
		axios
			.post('/graphql', {
				query
			})
			.then(res => {
				// debugger
				context.commit(
					'SET_OFFERS',
					res.data.data.paginateOffer.offers
				);
				context.commit(
					'SET_OFFERS_COUNT',
					res.data.data.paginateOffer.total
				);
				context.commit('loading/SET', false, {
					root: true
				});
			});
	}
};

const mutations = {
	SET_OFFERS(state, payload) {
		// payload.forEach(item => {
		//     if (!item.images.length) {
		//         let offer = item.offers.find(offer => offer.images.length);
		//         if (offer) {
		//             item.images.push(offer.images[0]);
		//         }
		//     }
		// });

		state.active = payload;
	},
	SET_OFFERS_COUNT(state, payload) {
		state.count = payload;
	},
	CLEAR(state) {
		state.active = null;
		state.count = 0;
	}
};

export default {
	namespaced: true,
	state,
	getters,
	actions,
	mutations
};
