const state = {
    item: null
};

const getters = {
    item() {
        return state.item;
    }
};

const actions = {
    get(context, payload) {
        let query = `
            {
                offer (id: [${payload.id}]){
                    id
                    name
                    prices{
                        sum
                        discount
                    }
                    images{
                        seoname
                    }
                    values{
                        id
                        name
                        display
                        property{
                            id
                            name
                            seoname
                            type_id
                            numbering
                            group{
                                id
                                name
                             numbering
                            }
                        }
                    }
                }
            }`;
        // console.log(query);
        context.commit('loading/SET', true, { root: true });
        axios
            .post('/graphql', {
                query
            })
            .then(res => {
                // debugger;
                context.commit('GET', res.data.data.offer[0]);
                context.commit('loading/SET', false, {
                    root: true
                });
            });
        // TODO обработать catch во всех местах
    }
};

const mutations = {
    GET(state, payload) {
        state.item = payload;
    },
    CLEAR(state) {
        state.item = null;
    }
};

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
};
