const state = {
    cart: [],
    favorites: []
};

const getters = {
    cartIds() {
        return state.cart.map(item => item.id);
    },
    favoritesIds() {
        return state.favorites.map(item => item.id);
    }
};

const actions = {
    getOffers(context, payload) {
        let ids = localStorage.getItem(payload.storage);
        window.scrollTo({ top: 0 });
        if (ids) {
            ids = JSON.parse(ids);
            let query = `
                {
                    offer(id: [${ids}]) {
                        id
                        name
                        prices{
                            sum
                            discount
                        }
                        values{
                            id
                            name
                            property{
                                id
                                name
                                seoname
                                type_id
                                group{
                                id
                                name
                                numbering
                                }
                                numbering
                            }
                        }
                    }
                }`;

            // console.log(query);
            context.commit(
                'loading/SET',
                { status: true, show: false },
                { root: true }
            );
            axios
                .post('/graphql', {
                    query
                })
                .then(res => {
                    // debugger
                    context.commit('SET_OFFERS', {
                        storage: payload.storage,
                        offers: res.data.data.offer
                    });
                    context.commit(
                        'loading/SET',
                        { status: false, show: true },
                        {
                            root: true
                        }
                    );
                });
        }
    }
};

const mutations = {
    RESET(state, payload) {
        state[payload.storage] = [];
        localStorage.removeItem([payload.storage]);
    },
    CHANGE(state, payload) {
        // debugger;
        let localState = state[payload.storage];
        let ids = localState.map(item => item.id);
        if (!ids.includes(payload.offer.id)) {
            localState.push(payload.offer);
        } else {
            let index = localState.findIndex(
                item => item.id === payload.offer.id
            );
            localState.splice(index, 1);
        }
        this.commit('storage/SET_OFFERS', {
            storage: payload.storage,
            offers: localState
        });
    },
    SET_OFFERS(state, payload) {
        // debugger;
        state[payload.storage] = payload.offers;
        localStorage.setItem(
            payload.storage,
            JSON.stringify(state[payload.storage].map(item => item.id))
        );
    }
};

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
};
